import { NextResponse } from "next/server";

export async function GET(req: Request) {
  const { searchParams } = new URL(req.url);
  const fdcId = searchParams.get("fdcId");

  if (!fdcId) {
    return NextResponse.json({ error: "Missing fdcId" }, { status: 400 });
  }

  const apiKey = process.env.USDA_API_KEY;
  if (!apiKey) {
    return NextResponse.json({ error: "Missing USDA key" }, { status: 500 });
  }

  const res = await fetch(
    `https://api.nal.usda.gov/fdc/v1/food/${fdcId}?api_key=${apiKey}`
  );

  if (!res.ok) {
    return NextResponse.json({ error: "USDA failed" }, { status: 500 });
  }

  const data = await res.json();

  return NextResponse.json(data);
}
